.header_new {
  z-index: 8;
  background-color: #211643;
  color: #ffffff;
  font-size: 16px;
  position: relative;
  background-color: #211643;
}

.header_new-i {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  top: 96px;
  transform: translateY(-96px);
  transition: transform .3s ease-in-out;
}

@media screen and (min-width: 1025px) {
  .header_new-i {
    top: 144px;
    transform: translateY(-144px);
  }
}

.header_new.is-scroll .header_new-i {
  top: 0;
  position: fixed;
}

.header_new.is-fixed .header_new-i {
  transform: translateY(0);
}

.header_new-content {
  max-width: 1356px;
  margin: 0 auto;
}

.header_new-topline {
  background-color: #100926;
  padding: .875em;
  font-size: .875em;
  overflow: hidden;
  line-height: 1.43;
}

.is-fixed .header_new-topline {
  display: none;
}

@media screen and (min-width: 1025px) {
  .header_new-topline {
    display: none;
  }
}

.header_new-topline-language {
  float: left;
  list-style: none;
  color: rgba(255, 255, 255, 0.6);
}

.header_new-topline-language li {
  display: inline-block;
}

.header_new-topline-language li + li {
  margin-left: 14px;
}

.header_new-topline-support {
  float: right;
  font-weight: 500;
  color: #ffffff;
}

.header_new-rss_button, .header_new-login_button {
  color: #ffffff;
  fill: #ffffff;
  transition: all .3s ease;
  box-sizing: border-box;
  text-align: center;
  cursor: pointer;
}

.header_new-rss_button:hover, .header_new-login_button:hover {
  color: #d9265e;
}

.header_new-rss_button {
  display: none;
  width: 1.5em;
}

.header_new-rss_button img,
.header_new-rss_button svg {
  width: inherit;
}

@media screen and (min-width: 1025px) {
  .header_new-rss_button {
    display: inline-block;
  }
}

.header_new-login_button {
  width: 2em;
  min-width: 2em;
  height: 2em;
  padding-top: .5em;
  font-size: 1em;
  margin-left: 1.25em;
}

.header_new-login_button svg {
  width: 1em;
}

.header_new-search {
  display: none;
  align-items: center;
  justify-content: flex-end;
  float: right;
  min-height: 1.125em;
  padding: .5em .8em .5em 1em;
  border-radius: 1em;
  background-color: #211643;
  transition: all .3s ease;
  width: 160px;
}

.header_new-search.is_focused {
  background-color: #ffffff;
  width: 100%;
}

.header_new-search-input {
  background-color: transparent;
  border: none;
  outline: none;
  font-size: .8em;
  color: rgba(153, 153, 153, 0.6);
  margin-right: .5em;
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: calc(100% - 1.625em);
}

@media screen and (min-width: 1025px) {
  .header_new-search {
    display: flex;
  }
  .header_new-search-open_modal {
    display: none;
  }
}

.header_new-search-button {
  min-width: 1.125em;
  width: 1.125em;
  height: 1.125em;
  font-size: 1em;
  padding: 0;
  color: #ffffff;
  cursor: pointer;
  background: none;
  border: none;
  display: inline-block;
}

.is_focused .header_new-search-button {
  color: rgba(153, 153, 153, 0.6);
}

.header_new-search-wrap {
  margin-left: auto;
}

@media screen and (min-width: 1025px) {
  .header_new-search-wrap {
    min-width: 250px;
  }
}

.header_new-menu_button {
  font-size: 1.5em;
  margin-right: 1.5em;
  border: none;
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
  cursor: pointer;
  width: 1em;
  height: 1em;
  background: none;
  position: relative;
  vertical-align: middle;
  transition: all .3s ease;
  color: currentColor;
}

.header_new-menu_button:hover {
  color: #d9265e;
}

.header_new-menu_button.active {
  border-color: currentColor;
}

.header_new-menu_button::after {
  content: '';
  position: absolute;
  background-color: currentColor;
  transition: inherit;
  opacity: 0;
  left: 0.175em;
  top: 50%;
  width: 0.25em;
  margin-top: -0.125em;
  height: 0.25em;
}

.header_new-menu_button.active::after {
  width: calc(100% - 0.375em);
  font-size: .8em;
  opacity: 1;
}

.header_new-menu_button::before {
  transition: inherit;
  border-radius: 50%;
  content: '';
  position: absolute;
  background-color: currentColor;
  color: currentColor;
  width: 0.25em;
  height: 0.25em;
  top: calc(50% - 0.25em / 2);
  left: calc(50% - 0.25em / 2);
  box-shadow: -0.3125em -0.3125em, 0 -0.3125em, 0.3125em -0.3125em, -0.3125em 0, 0.3125em 0, -0.3125em 0.3125em, 0 0.3125em, 0.3125em 0.3125em;
}

.header_new-menu_button.active::before {
  border-radius: 0;
  background-color: transparent;
  transform: rotate3d(0, 0, 1, -45deg) scale3d(0.8, 0.8, 0.8);
  box-shadow: -0.3125em -0.3125em, -0.06875em -0.3125em, 0.175em -0.3125em, -0.3125em -0.06875em, 0 0 transparent, -0.3125em 0.175em, 0 0 transparent, 0 0 transparent;
}

.header_new-top {
  padding: 1em;
  background-color: #211643;
}

.header_new-top-content {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
}

@media screen and (min-width: 1025px) {
  .header_new-top {
    background-color: #100926;
    padding: 1em 1.5em;
  }
}

.header_new-top-logo {
  max-height: 34px;
}

@media screen and (min-width: 1025px) {
  .header_new-top-logo {
    display: none;
  }
}

.header_new-top-main_menu, .header_new-top-sub_menu {
  display: none;
  position: relative;
  min-height: 2em;
  line-height: 2em;
  border-left: 1px solid rgba(255, 255, 255, 0.15);
  padding-left: 1.5em;
  margin-left: 1.5em;
  transition: all .3s ease;
}

@media screen and (min-width: 1025px) {
  .header_new-top-main_menu, .header_new-top-sub_menu {
    display: block;
  }
}

.header_new-top-main_menu-item, .header_new-top-sub_menu-item {
  color: rgba(255, 255, 255, 0.6);
  display: inline-block;
}

.header_new-top-main_menu-item.active, .header_new-top-main_menu-item:hover, .header_new-top-sub_menu-item.active, .header_new-top-sub_menu-item:hover {
  color: #ffffff;
}

.header_new-top-main_menu-item + .header_new-top-main_menu-item, .header_new-top-sub_menu-item + .header_new-top-main_menu-item, .header_new-top-main_menu-item + .header_new-top-sub_menu-item, .header_new-top-sub_menu-item + .header_new-top-sub_menu-item {
  padding-left: .8em;
}

.header_new-top-main_menu-list, .header_new-top-sub_menu-list {
  white-space: nowrap;
}

.header_new-top-main_menu-link, .header_new-top-sub_menu-link {
  font-size: .8em;
}

.header_new-top-sub_menu-dropdown {
  display: none;
  position: absolute;
  white-space: normal;
  padding: 1.5em;
  top: 100%;
  left: 100%;
  min-width: 10em;
  max-width: 12em;
  margin-left: -1.9em;
  border-radius: .25em;
  background-color: #ffffff;
  box-shadow: 0 0 49px 0 rgba(0, 0, 0, 0.15);
}

.header_new-top-sub_menu-item {
  position: relative;
}

.header_new-top-sub_menu-item-title {
  cursor: pointer;
  font-size: .8em;
  position: relative;
  padding-right: 1.2em;
}

.header_new-top-sub_menu-item-title.is_open {
  color: #ffffff;
}

.header_new-top-sub_menu-item-title::before {
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 6px 4px 0 4px;
  border-color: #ffffff transparent transparent transparent;
  content: '';
  position: absolute;
  right: .125em;
  top: 50%;
  margin-top: -3px;
}

.header_new-top-sub_menu-link {
  display: block;
  line-height: 1.6;
  color: #000000;
  opacity: .6;
}

.header_new-top-sub_menu-link + .header_new-top-sub_menu-link {
  margin-top: .4em;
}

.header_new-top-sub_menu-link:hover {
  opacity: 1;
}

.header_new-top-main_menu {
  padding-left: 2em;
  flex: 1;
  max-width: calc(100% - 41em);
}

.header_new-top-main_menu-list {
  display: flex;
  justify-content: space-between;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-right: 1.3em;
}

.header_new-top-main_menu-dropdown {
  display: none;
  position: absolute;
  top: 100%;
  left: 100%;
  margin-top: 1em;
  margin-left: -3em;
  padding: 1.5em;
  background-color: #100926;
}

.header_new-top-main_menu-dropdown .header_new-top-main_menu-item {
  padding: 0;
  font-size: .8em;
  white-space: nowrap;
  height: 1.4em;
}

.header_new-top-main_menu-dropdown_button {
  float: right;
  display: none;
  margin-left: .8em;
  width: 1.5em;
  min-width: 1.5em;
  height: 2em;
  display: inline-block;
  position: relative;
  overflow: hidden;
  text-indent: 9999px;
  transition: inherit;
  cursor: pointer;
  opacity: .6;
}

.header_new-top-main_menu-dropdown_button:hover, .header_new-top-main_menu-dropdown_button.active {
  opacity: 1;
}

.header_new-top-main_menu-dropdown_button:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  color: #ffffff;
  background-color: currentColor;
  width: .25em;
  height: .25em;
  box-shadow: -.5em 0, .5em 0;
}

.header_new-top-main_menu-link {
  color: inherit;
  cursor: pointer;
  transition: inherit;
  display: inline-block;
}
